import css from './styles.css';
import React from 'react';

const Drawer = (props) => {
  const {
    children,
    ...rest
  } = props;

  const drawerContent =  React.useRef(null);

  const[isOpen, toggleDrawer] = React.useState(false); 

  const openClass = isOpen ? css.open : css.closed;

  const array = [css.inner, openClass].join(' ');


  React.useEffect(() => {
    console.log(drawerContent.current.clientHeight);
  });
  
  return (
    <div className="Drawer" {...rest}>
      <div className={array} styles={ drawerContent.current && { height: `{$drawerContent.current.clientHeight}px`}}>
        <div ref={drawerContent} id="Drawer-content">
          {children}
        </div>
      </div>
      <button onClick={() => toggleDrawer(!isOpen)}>
        {isOpen ? 'Close Drawer' : 'Open Drawer'}
      </button>
    </div>
  )
}

export default Drawer;